importScripts('https://unpkg.com/leaflet@1.3.1/dist/leaflet.js');

self.addEventListener('message', (event) => {
  const { type } = event;
  if (type === 'load-tile-layer') {
    const { MAPBOX_API_KEY } = event;
    const loadedTileLayer = L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.jpg70?access_token={mapboxToken}', {
      mapboxToken: MAPBOX_API_KEY,
      maxZoom: 18,
      attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, '
        + '<a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, '
        + 'Imagery Â© <a href="https://www.mapbox.com/">Mapbox</a>',
      id: 'mapbox.streets',
    });
    self.postMessage({
      type: 'load-tile-layer',
      loadedTileLayer,
    });
  }
});
